import {
  createTheme,
  InputLabel,
  MenuItem,
  Select,
  ThemeProvider,
} from "@mui/material";
import React, { useState } from "react";
import { DownArrow, GreyDownArrow } from "../SVG/DownArrow";

export function FilledSelectInput(props) {
  const [value, setValue] = useState("");

  const theme = createTheme({
    components: {
      MuiInputLabel: {
        styleOverrides: {
          asterisk: {
            color: "#FF3700",
          },
        },
      },
    },
  });
  return (
    <div>
      <ThemeProvider theme={theme}>
        <InputLabel required={props.required}>{props.select_label}</InputLabel>
        <Select
          fullWidth
          value={props.value}
          //   onChange={}
          IconComponent={() => (
            <div style={{padding:"10px"}}>
              <GreyDownArrow />
            </div>
          )}
          displayEmpty
          inputProps={{ "aria-label": "Without label" }}
        >
          <MenuItem value={props.value}>
            <em>--Select--</em>
          </MenuItem>
          {props?.select_menuitem.map((data) => {
            console.log("daata", data);
            return <MenuItem value={data}>{data}</MenuItem>;
          })}
        </Select>
      </ThemeProvider>
    </div>
  );
}

