import {
  AppBar,
  Collapse,
  Divider,
  Drawer,
  Grid,
  IconButton,
  InputAdornment,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  OutlinedInput,
  TextField,
  Toolbar,
} from "@mui/material";
import React, { useState, useEffect } from "react";
import DashoboardIcon from "../SVG/DashoboardIcon";
import GroupPersonIcon from "../SVG/GroupPersonIcon";
import {SideArrowIcon,LightSideArrowIcon} from "../SVG/SideArrowIcon";
import LeadIcon from "../SVG/LeadIcon";
import TaskIcon from "../SVG/TaskIcon";
import MessageIcon from "../SVG/MessageIcon";
import WebsiteIcon from "../SVG/WebsiteIcon";
import FilterIcon from "../SVG/FilterIcon";
import BrochuresIcon from "../SVG/BrochuresIcon";
import SocialCastIcon from "../SVG/SocialCastIcon";
import RightUserIcon from "../SVG/RightUserIcon";
import SettingIcon from "../SVG/SettingIcon";
import SearchIcon from "../SVG/SearchIcon";
import SmsIcon from "../SVG/SmsIcon";
import BellIcon from "../SVG/BellIcon";
import LogoutIcon from "../SVG/LogoutIcon";
import { DownArrow } from "../SVG/DownArrow";
import UpArrow from "../SVG/UpArrow";
import { useNavigate } from "react-router-dom";
import CreateNewLead from "./Leads/CreateNewLead";
import ViewLeads from "./Leads/ViewLeads";
import UnverifiedLeads from "./Leads/UnverifiedLeads";
import EditLead from "./Leads/EditLead";

function BasicPage() {
  const drawerWidth = 300;
  const navigate = useNavigate();

  const [leadsopen, setLeadsopen] = useState(false);

  const [breadCrumb, setBreadCrumb] = useState("");

  const subComponentHandler = () => {
    if (window.location.pathname === "/create-new-lead") {
      return <CreateNewLead />;
    }
    if (window.location.pathname === "/view-leads") {
      return <ViewLeads />;
    }
    if (window.location.pathname === "/unverified-leads") {
      return <UnverifiedLeads />;
    }
    if (window.location.pathname === "/view-leads/edit") {
      return <EditLead />;
    }
  };
  const breadCrumbHandler = () => {
    if (window.location.pathname === "/create-new-lead") {
    }
    if (
      window.location.pathname === "/view-leads" ||
      window.location.pathname === "/unverified-leads"
    ) {
      setBreadCrumb("Dashboard / Leads / Create New Lead");
    }
  };
  useEffect(() => {
    breadCrumbHandler();
  }, []);

  return (
    <div>
      <AppBar
        position="fixed"
        sx={{
          width: `calc(100% - ${drawerWidth}px)`,
          ml: `${drawerWidth}px`,
          backgroundColor: "#FFFFFF",
          height: "100%",
        }}
      >
        <Toolbar sx={{ padding: 2 }}>
          <Grid
            container
            spacing={2}
            sx={{ display: "flex", alignItems: "center" }}
          >
            <Grid item xs={6}>
              <div className="breadcrumb-text">{breadCrumb}</div>
            </Grid>
            <Grid item xs={4}>
              <OutlinedInput
                fullWidth
                variant="standard"
                sx={{
                  "& fieldset": { border: "none" },
                  "& ::placeholder": {
                    display: "flex",
                    marginBottom: 5,
                    alignItems: "center",
                    fontSize: 16,
                    fontWeight: 300,
                  },
                  border: "0.5px solid #ABB2BB",
                  borderRadius: 30,
                }}
                startAdornment={
                  <InputAdornment position="start">
                    <div>
                      <SearchIcon />
                    </div>
                  </InputAdornment>
                }
                placeholder=" Search"
              />
            </Grid>
            <Grid
              item
              xs={2}
              sx={{ display: "flex", alignItems: "center", paddingLeft: 2 }}
            >
              <div className="nav-icon">
                <SmsIcon />
              </div>
              <div className="nav-icon">
                <BellIcon />
              </div>
              <div className="nav-icon">
                <LogoutIcon />
              </div>
            </Grid>
          </Grid>
        </Toolbar>
        <div className="container">{subComponentHandler()}</div>
      </AppBar>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            width: drawerWidth,
            boxSizing: "border-box",
          },
        }}
        variant="permanent"
        anchor="left"
      >
        <div className="sidebar-head">
          <div style={{ fontWeight: 700, fontSize: 26 }}>RealtyCast</div>
          <div style={{ fontWeight: 400, fontSize: 18, color: "#ABB2BB" }}>
            Manage your Dashboard from Here
          </div>
        </div>
        <Divider />
        <List>
          <ListItem>
            <DashoboardIcon /> &nbsp; Dashboard
          </ListItem>

          <ListItem>
            <SideArrowIcon /> &nbsp; Getting Started
          </ListItem>

          <ListItem>
            <GroupPersonIcon /> &nbsp; Partners
          </ListItem>

          <ListItem
            sx={{ display: "flex", alignItems: "center" }}
            onClick={() => {
              setLeadsopen(!leadsopen);
            }}
          >
            <ListItemButton sx={{ padding: 0 }}>
              <LeadIcon /> &nbsp; Leads
            </ListItemButton>
            <div style={{ alignItems: "right" }}>
              {leadsopen ? <UpArrow /> : <DownArrow />}
            </div>
          </ListItem>
          <Collapse in={leadsopen} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItemButton
                onClick={() => {
                  navigate("./create-new-lead");
                }}
                className="lead-list"
                sx={{ pl: 7 }}
              >
                <ListItemText
                  className="lead-list-text"
                  primary="Create New Lead"
                />
              </ListItemButton>
              <ListItemButton
                onClick={() => {
                  navigate("./view-leads");
                }}
                className="lead-list"
                sx={{ pl: 7 }}
              >
                <ListItemText className="lead-list-text" primary="View Leads" />
              </ListItemButton>
              <ListItemButton
                onClick={() => {
                  navigate("./unverified-leads");
                }}
                className="lead-list"
                sx={{ pl: 7 }}
              >
                <ListItemText
                  className="lead-list-text"
                  primary="Unverified Leads"
                />
              </ListItemButton>
              <ListItemButton className="lead-list" sx={{ pl: 7 }}>
                <ListItemText className="lead-list-text" primary="Pipeline" />
              </ListItemButton>
              <ListItemButton className="lead-list" sx={{ pl: 7 }}>
                <ListItemText className="lead-list-text" primary="Archive" />
              </ListItemButton>
              <ListItemButton className="lead-list" sx={{ pl: 7 }}>
                <ListItemText
                  className="lead-list-text"
                  primary="Get leads from Bizooda"
                />
              </ListItemButton>
            </List>
          </Collapse>

          <ListItem>
            <TaskIcon /> &nbsp; Tasks
          </ListItem>

          <ListItem>
            <MessageIcon /> &nbsp; Marketing
          </ListItem>

          <ListItem>
            <WebsiteIcon /> &nbsp; My Website
          </ListItem>

          <ListItem>
            <FilterIcon /> &nbsp; Funnels
          </ListItem>

          <ListItem>
            <BrochuresIcon /> &nbsp; Brochures
          </ListItem>

          <ListItem>
            <SocialCastIcon /> &nbsp; Social Cast
          </ListItem>

          <ListItem>
            <RightUserIcon /> &nbsp; My Account
          </ListItem>

          <ListItem>
            <SettingIcon /> &nbsp; Settings
          </ListItem>
        </List>
      </Drawer>
    </div>
  );
}

export default BasicPage;
