import {
  Box,
  Button,
  Paper,
  Step,
  StepLabel,
  Stepper,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import { ContainedButtons, OutlinedButtons } from "../../Component/Button";
import AgentInformationForm from "../../SubComponent/AgentInformationForm";
import BasicInformationForm from "../../SubComponent/BasicInformationForm";
import DetailedInformationForm from "../../SubComponent/DetailedInformationForm";
import { LightSideArrowIcon } from "../../SVG/SideArrowIcon";

function EditLead() {
  const [activeStep, setActiveStep] = useState(0);

  const [skipped, setSkipped] = React.useState(new Set());

  const steps = [
    "Basic Information",
    "Detailed Information",
    "Agent Information",
    "Lead Requirements",
  ];

  const isStepOptional = (step) => {
    return step === 1;
  };

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    // setSkipped(newSkipped);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleSkip = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped((prevSkipped) => {
      const newSkipped = new Set(prevSkipped.values());
      newSkipped.add(activeStep);
      return newSkipped;
    });
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const EditFormStephandler = () => {
    if (activeStep === 0) {
      return <BasicInformationForm />;
    }
    if (activeStep === 1) {
      return <DetailedInformationForm />;
    }
    if (activeStep === 2) {
      return <AgentInformationForm />;
    }
    
  };
  return (
    <Paper
      sx={{
        width: "100%",
        height: "1074px",
        background: "#FFFFFF",
        boxShadow: "-1px 2px 21px -5px rgba(0, 0, 0, 0.25)",
        borderRadius: "10px",
      }}
    >
      <div className="card-container">
        <div className="containerHead">
          <p>Edit Lead</p>
        </div>
        <div className="editLeadStepper">
          <Box sx={{ width: "100%" }}>
            <Stepper activeStep={activeStep}>
              {steps.map((label, index) => {
                const stepProps = {};
                const labelProps = {};

                return (
                  <Step key={label} {...stepProps}>
                    <StepLabel {...labelProps}>{label}</StepLabel>
                  </Step>
                );
              })}
            </Stepper>
          </Box>
        </div>
        <div className="containerContent">
          <div style={{ marginTop: 35 }}>{EditFormStephandler()}</div>
          <div
            style={{
              display: "flex",
              justifyContent: "end",
              marginTop: "45px",
            }}
          >
            <div>
              <OutlinedButtons button_name="Cancel" color="#92959A" />
            </div>
            <div style={{ marginLeft: "15px" }} onClick={handleNext}>
              <ContainedButtons
                button_name="Save & Next"
                endIcon={<LightSideArrowIcon />}
              />
            </div>
          </div>
        </div>
      </div>
    </Paper>
  );
}

export default EditLead;
