import { ClassNames } from "@emotion/react";
import {
  createTheme,
  FormControl,
  Input,
  InputAdornment,
  InputLabel,
  TextField,
  ThemeProvider,
} from "@mui/material";
import React from "react";
import SearchIcon from "../SVG/SearchIcon";

export function InputField(props) {
  const theme = createTheme({
    components: {
      MuiInputLabel: {
        styleOverrides: {
          asterisk: {
            color: "#FF3700",
          },
        },
      },
    },
  });
  return (
    <>
      <div align="left" style={{}}>
        <ThemeProvider theme={theme}>
          <InputLabel
            htmlFor={props.input_id}
            required={props.required}
            sx={{ color: `${props.color}` }}
          >
            {props.input_label}
          </InputLabel>
          <TextField
            disabled
            className={props.className}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  {props.input_icon}
                </InputAdornment>
              ),
            }}
            id={props.input_id}
            placeholder={props.input_placeholder}
            fullWidth
            aria-describedby="my-helper-text"
            type={props.input_type}
          />
        </ThemeProvider>
      </div>
    </>
  );
}

export function DarkInputField(props) {
  return (
    <>
      <div align="left" style={{}}>
        <InputLabel htmlFor={props.input_id}>{props.input_label}</InputLabel>
        <TextField
          sx={{ borderColor: `${props.borderColor}` }}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                {props.input_icon}
              </InputAdornment>
            ),
          }}
          id={props.input_id}
          placeholder={props.input_placeholder}
          fullWidth
          aria-describedby="my-helper-text"
          type={props.input_type}
        />
      </div>
    </>
  );
}
