import { Box, Grid, Modal } from "@mui/material";
import React, { useState } from "react";
import { OutlinedButtons, ContainedButtons } from "./Button";

function Alert(props) {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "500px",
    height: "auto",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    // justifyContent: "center",
    p: 4,
    background: "#FFFFFF",
    boxShadow: "1px 7px 21px -9px rgba(0, 0, 0, 0.25)",
    borderRadius: "20px",
  };

  function afterOpenModal(e) {
    props.onAfterOpen(e);
  }

  function onModalClose(event) {
    //   let data = { name: 'example', type: 'closed from child' };
    console.log(event);
    props.onCloseModal(event);
  }

  return (
    <div>
      <Modal
        open={props.IsModalOpened}
        onOpen={(e) => afterOpenModal(e)}
        // onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <div>
            {props.verified_lead ? (
              <svg
                width="300"
                height="300"
                viewBox="0 0 300 300"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
              >
                <rect width="300" height="300" fill="url(#pattern0)" />
                <defs>
                  <pattern
                    id="pattern0"
                    patternContentUnits="objectBoundingBox"
                    width="1"
                    height="1"
                  >
                    <use xlinkHref="#image0_3_7583" transform="scale(0.002)" />
                  </pattern>
                  <image
                    id="image0_3_7583"
                    width="500"
                    height="500"
                    xlinkHref="data:image/png;base64,"
                  />
                </defs>
              </svg>
            ) : (
              <svg
                width="255"
                height="255"
                viewBox="0 0 255 255"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
              >
                <rect width="255" height="255" fill="url(#pattern0)" />
                <defs>
                  <pattern
                    id="pattern0"
                    patternContentUnits="objectBoundingBox"
                    width="1"
                    height="1"
                  >
                    <use xlinkHref="#image0_3_10246" transform="scale(0.002)" />
                  </pattern>
                  <image
                    id="image0_3_10246"
                    width="500"
                    height="500"
                    xlinkHref="data:image/png;base64,"
                  />
                </defs>
              </svg>
            )}
          </div>
          <div
            style={{ fontWeight: 700, fontSize: "30px", lineHeight: "41px" }}
          >
            Are You Sure?
          </div>
          <div
            style={{ fontWeight: 400, fontSize: "20px", lineHeight: "27px" }}
          >
            {props.verified_lead
              ? "You want to move this lead into verified lead section?"
              : "Your Lead will get delete permanently from dashboard!"}
          </div>
          <Grid container spacing={2} sx={{ mt: 5 }}>
            <Grid item xs={6} onClick={(e) => onModalClose(e)}>
              <OutlinedButtons
                color="#46448D"
                button_name="Back"
                button_variant="outlined"
              />
            </Grid>
            <Grid item xs={6}>
              <ContainedButtons
                button_name="Yes, Delete!"
                button_variant="contained"
              />
            </Grid>
          </Grid>
        </Box>
      </Modal>
    </div>
  );
}

export default Alert;
