import React from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import BasicPage from "../Pages/BasicPage";
import CreateNewLead from "../Pages/Leads/CreateNewLead";
import EditLead from "../Pages/Leads/EditLead";
import UnverifiedLeads from "../Pages/Leads/UnverifiedLeads";
import ViewLeads from "../Pages/Leads/ViewLeads";

function Routing() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<BasicPage />}>
          <Route path="create-new-lead" element={<CreateNewLead />} />

          <Route path="view-leads" element={<ViewLeads />} />

          <Route path="unverified-leads" element={<UnverifiedLeads />} />

          <Route path="view-leads/edit" element={<EditLead />} />

        </Route>
      </Routes>
    </BrowserRouter>
  );
}

export default Routing;
