import { Button } from "@mui/material";
import React from "react";

export function ContainedButtons(props) {
  return (
    <div>
      <Button  fullWidth startIcon={props.button_icon} endIcon={props.endIcon} variant="contained">{props.button_name}</Button>
    </div>
  );
}
export function OutlinedButtons(props) {
  return (
    <div>
      <Button sx={{color:`${props.color}`}}  fullWidth startIcon={props.button_icon} endIcon={props.endIcon} variant="outlined">{props.button_name}</Button>
    </div>
  );
}

